	module.exports = {"data":{"markdownRemark":{"html":"<p>夕方、ある学生が私に来て、彼は「話す」と言いました。現在の学生には奇妙なアイデアがあります。この絶え間なく変化する社会において、そして常に緩やかな社会構造において、学生はそれ自身で選択する必要があります。</p>\n<p>開発の道のりで、多くの学生は、中学校を離れるとすぐに一連の重大な生命問題に直面します。過去は親と教師によって管理されていました。社会文化が変わった今、学生が選択するのは自分の責任です。混乱し、選択と矛盾でいっぱいの世界に直面する方法を知らない多くの学生が確かにいます。多くの人々は、選択できないという点で重要な開発機会を失いました。早すぎる選択で開発の機会を失った人もいます。それは本当に選ぶのが悪いです。それは選ぶのが賢明ではありません。時には、よく選択しないでください。若者が意思決定をするのは簡単ではありません。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0049.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0049.md","frontmatter":{"title":"3月4日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0049.md absPath of file >>> MarkdownRemark"}}

